import Vue from 'vue';
import Vuex from 'vuex';

import resume from './cv';
import app from './global';

Vue.use(Vuex);

const debug = process.env.NODE_ENV !== 'production';

export default new Vuex.Store({
    modules: {
        cv: resume,
        app: app
    },
    strict: debug
});




